.App {
  text-align: center;
  font-family: Arial;
}

.container {
  background-color: steelblue;
  padding: 5px;
  width: 280px;
  height: 405px;
  padding: 0px;
  margin: 10vh auto auto auto;
}

.display {
  width: 275px;
  height: 35px;
  background-color: black;
  color: white;
  text-align: right;
  line-height: 35px;
  font-weight: bolder;
  vertical-align: text-bottom;
  padding: 0px 5px 0px 0px;
}

.display2 {
  width: 275px;
  height: 20px;
  background-color: black;
  color:khaki;
  text-align: right;
  line-height: 20px;
  font-weight: bolder;
  font-size: 10px;
  vertical-align: text-bottom;
  padding: 0px 5px 0px 0px;
  word-wrap: break-word;
}

.button {
  display: inline;
  width: 70px;
  height: 70px;
  margin: 0px;
  background-color: #4b66b3;
  font-weight: bolder;
  color: white;
}

.button:active {
  background-color: sandybrown;
  color: #4b66b3;
}

#add, #equals {
  float: right;
  height: 140px;
  margin: 0px;
}

#zero {
  width: 140px;
}

.bigDisplay {
  height: 40px;
}

.biggerDisplay {
  height: 60px;
}
